@use '../../bpk-mixins/tokens';
@use '../../bpk-mixins/breakpoints';

.bpk-phone-input {
  display: flex;
  column-gap: tokens.bpk-spacing-md();

  @include breakpoints.bpk-breakpoint-mobile {
    flex-wrap: wrap;

    &__phone-number {
      width: 100%;
      margin-top: tokens.bpk-spacing-base();
    }
  }

  &__phone-number {
    flex: auto;
  }

  &__label {
    margin-bottom: tokens.bpk-spacing-md();
  }
}
