@use '../../bpk-mixins/icons';

.bpk-icon {
  &--align-to-button {
    @include icons.bpk-icon-sm--align-to-button;
  }

  &--align-to-large-button {
    @include icons.bpk-icon-lg--align-to-large-button;
  }

  &--rtl-support {
    @include icons.bpk-icon--rtl-support;
  }
}
