@use '../../bpk-mixins/tokens';
@use '../../bpk-mixins/forms';
@use '../../bpk-mixins/margins';
@use '../../bpk-mixins/utils';

.bpk-form-validation {
  @include forms.bpk-form-validation;

  &__container {
    @include forms.bpk-form-validation__container;
  }

  &__icon {
    @include utils.bpk-themeable-property(
      fill,
      --bpk-form-validation-icon-fill,
      tokens.$bpk-form-validation-color
    );
    @include margins.bpk-margin-trailing(tokens.bpk-spacing-sm());
  }

  &--appear {
    @include forms.bpk-form-validation--appear;
  }

  &--is-checkbox {
    @include forms.bpk-form-validation--is-checkbox;
  }
}
